<template>
<div >
	<headA></headA>
	<section v-for="obj in mba">
		<h3 class="bg3">{{obj.hp}}</h3>
		<div v-for="sonobj in obj.objdd">
			<router-link :to="sonobj.href"><p class="mbap">{{sonobj.stiong}}</p></router-link>
		</div>
	</section>
	<footerA></footerA>
</div>
	
</template>

<script lang="babel">
import footerA from "./common/footer"; 
import headA from "./common/haed"; 
	export default{
		data(){
			return{
				mba:[],
			}
		},
        components:{
      	footerA,
      	headA
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	_this.$http({
        		method:"get",
        		url:"./static/json/mba.json",
        		params:{},
        	})
        	.then(function(data){
				console.log(data);
				_this.mba=data.body;
			},function(err){
        		alert("请求失败");
        	});
        	
        }

    }
</script>
<style  >
.bg3{background: url("../assets/images/bg3.png") no-repeat 0 0 / 100% auto;height: 0.7rem;padding-left: 0.60rem;line-height: 0.7rem;font-weight: 400;}
.mbap{padding:0 0 0 0.53rem;margin:0;line-height: 0.68rem;font-size: 0.24rem;border-top: 0.01rem solid #fff;border-bottom: 1px solid #d4d4d4;}
</style>



