import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import indexA from '@/components/index'
import brand from '@/components/brand'
import Brandstory from '@/components/common/Brandstory'
import introduce from '@/components/introduce'
import activity from '@/components/activity'
import details from '@/components/details'
import free from '@/components/common/free'
import mba from '@/components/mba'
import news from '@/components/news'
import audition from '@/components/audition'

Vue.use(Router);
Vue.use(Resource);

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: indexA
    },
    {
      path: '/brand',
      name: '品牌',
      component: brand
    },
    {
      path: '/Brandstory/:id/:jh',
      name: '详情',
      component: Brandstory
    },
    {
      path: '/details/:id/:jh',
      name: '活动详情',
      component: details
    },
    {
      path: '/introduce',
      name: '课程介绍',
      component: introduce
    },
    {
      path: '/activity',
      name: '课程介绍',
      component: activity
    },
    {
      path: '/free/:id/:jh',
      name: '免费培训',
      component: free
    },
    {
      path: '/mba',
      name: '课程介绍',
      component: mba
    },
    {
      path: '/news',
      name: '新闻',
      component: news
    },
    {
      path: '/audition',
      name: '试听',
      component: audition
    }
  ]
})



