<template>
<div>
	<headA></headA>
	<div class="modeuf" v-for="obj in free">
		<span>{{obj.activity}}</span>
		<div>{{obj.admi}}</div>
		<img :src="obj.img"  />
		<p>{{obj.content}}</p>
		<section class="button2">报名参加</section>
	</div>
	<footerA></footerA>
</div>	
</template>
<script lang="babel">
import footerA from "./footer"; 
import headA from "./haed";
	export default{
		data(){
			return{
				free:[]
			}
		},
        components:{
        	footerA,
        	headA
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	let id=_this.$route.params.id;
			let name=_this.$route.params.name;
        	_this.$http({
        		method:"get",
        		url:"./static/json/free.json",
        		params:{},
        	})
        	.then(function(data){
				
				_this.free=data.body;
				console.log(_this.free);
			},function(err){
        		alert("请求失败");
        	});
        	
        }
  
    }
</script>
<style>
.modeuf span{color: #008dce;font-size: 0.30rem;line-height: 0.68rem;display: block;height: 0.68rem;}
.modeuf{padding: 0rem 0 0.61rem 0rem;background: url("../../assets/images/bg2_04.jpg") no-repeat 0 0 / 100% auto;}
.modeuf div{margin-left: 0.29rem;color: #009bda;font-size: 0.36rem;line-height: 1.12rem;}
.modeuf p{margin-left: 0.29rem;margin-top: 0.13rem;color: #453d3d;font-size: 0.24rem;line-height: 0.48rem;text-indent:2em;}
.button2{margin: 0.22rem 0 0 0.29rem;width: 5.65rem;height:0.72rem ;text-align: center;background: url("../../assets/images/buttom.png") no-repeat 0 0 / 100% auto;color: #fff;line-height: 0.72rem;font-size: 0.29rem;display: block;}
</style>


