<template>
<div >
	<headA></headA>
	<brand :brandArr="brandArr"></brand>
	<footerA></footerA>
</div>
	
</template>

<script lang="babel">
import footerA from "./common/footer"; 
import headA from "./common/haed"; 
import brand from "./common/brand";
	export default{
		data(){
			return{
			brandArr:[	
			],
			}
		},
        components:{
      	footerA,
      	headA,
      	brand
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	_this.$http({
        		method:"get",
        		url:"./static/json/brand.json?4",
        		params:{},
        	})
        	.then(function(data){
				console.log(data);
				_this.brandArr=data.body;
			},function(err){
        		alert("请求失败");
        	});
        	
        }

    }
</script>
<style  >

</style>



