<template>
	<div >
		<headA></headA>
		<section class="branbg">
			最终页标题
		</section>
		<section class="theme">
			<p>{{storyData.public}}</p>
			<p>{{storyData.public}}</p>
			<p>{{storyData.public}}</p>
			<p>{{storyData.public}}</p>
		</section>
		<footerA></footerA>
	</div>
</template>

<script lang="babel">
import footerA from "./footer"; 
import headA from "./haed"; 
	export default{
		data(){
			return{
				storyData:{
					
				}
			}
		},
        components:{
      	footerA,
      	headA      	
        },
        mounted(){
//			console.log(this.$http);
			let _this=this;//组件对象
			let id=_this.$route.params.id;
			let name=_this.$route.params.name;
			console.log(id)
			_this.$http({
				method:"get",
				url:"./static/json/story.json",
				params:{id:id},//发送的数据，get请求用属性params，post请求用属性body
			})
			.then(function(data){//成功的
//				console.log(data.data,data.body);
				_this.storyData=data.body;
			},function(err){//失败的
				alert("错误了请稍后再试")
			})
		}
  

    }
</script>

<style>
.branbg{background: url("../../assets/images/bg2_04.jpg") no-repeat 0 0 / 100% auto;height: 0.44rem;padding: 0.17rem 0 0 0.29rem;font-size: 0.30rem;line-height: 0.30rem;}
.theme{padding: 0.32rem 0 0 0.29rem;padding-bottom: 0.58rem;}
.theme p{margin-top: 0.38rem;font-size: 0.24rem;}
</style>


