<template>
	<div id="haed">
	<div class="navigation":class="{labelIn:showHideBool,labelOut:(!showHideBool && loadHideBool)}" >
		<p>
		 	<router-link to="/">网站首页</router-link>
			<router-link to="../brand">瑞德品牌</router-link>
			<router-link to="../mba">课程介绍</router-link>
			<router-link to="../introduce">活动资讯</router-link>
			<router-link to="../news">父母MBA</router-link>
			<router-link to="../activity">新闻中心</router-link>
			<router-link to="../audition">预约试听</router-link>
		</p>
	</div>
		<section class="head">
			<div class="label"  @click="showHideBool=true;loadHideBool=false;" @touchstart="listFun"> 				
			</div>
			<h1 class="logo">	
			</h1>
			<a href="tel:88888888">
			<div class="phone">		
			</div>
			</a>

		</section>
		<div class="soden" v-if="showHideBool" @click="showHideBool=false;loadHideBool=true;"@touchmove="showHideBool=false;loadHideBool=true;"@touchstart="falseFun"></div>
	</div>
</template>

<script lang="babel">
	import bus from "../../assets/js/bus";
	export default{
		data(){
			return {
				showHideBool:false,
				loadHideBool:false,
			
			}
		},
		methods:{
			listFun(){
				var showHideBool=true;
				var loadHideBool=false;
				bus.$emit("bool",showHideBool,loadHideBool)
			},
			falseFun(){
				var showHideBool=false;
				var loadHideBool=true;
				bus.$emit("bool",showHideBool,loadHideBool)
			}
		}
  }
</script>

<style>
	/*头部*/
.soden{position: absolute;width: 100%;height: 100%;z-index: 2;}
.navigation{width: 2.84rem;height: 100%;background: #3c3535;position: absolute;left: -2.84rem;z-index: 99;}
.navigation p a{width: auto;height: 0.84rem;padding:0.46rem 0 0 1.3rem;line-height: 0.31rem;color: #ffffff;font-size: 0.30rem;border-bottom: 1px solid #1e1818;border-top: 1px solid #584c4c;display: block;}
.navigation p a:nth-child(1){background: url("../../assets/images/tb1.png") no-repeat 0.40rem 0.26rem / auto auto;}
.navigation p a:nth-child(2){background: url("../../assets/images/tb2.png") no-repeat 0.40rem 0.26rem / auto auto;}
.navigation p a:nth-child(3){background: url("../../assets/images/tb3.png") no-repeat 0.40rem 0.26rem / auto auto;}
.navigation p a:nth-child(4){background: url("../../assets/images/tb4.png") no-repeat 0.40rem 0.26rem / auto auto;}
.navigation p a:nth-child(5){background: url("../../assets/images/tb5.png") no-repeat 0.40rem 0.26rem / auto auto;}
.navigation p a:nth-child(6){background: url("../../assets/images/tb6.png") no-repeat 0.40rem 0.26rem / auto auto;}
.navigation p a:nth-child(7){background: url("../../assets/images/tb7.png") no-repeat 0.40rem 0.26rem / auto auto;}
.head {
  width: auto;
  height: 0.76rem;
  background: url(../../assets/images/head_01.jpg);
  padding: 18px 0px 0px 30px;
  display: flex;
  background-size: 100% 100%;
}
.label {
  width: 0.34rem;
  height: 0.25rem;
  margin: 0.15rem 0.81rem 0rem 0rem;
  background: url("../../assets/images/label_10.png") no-repeat 0 0 / 100% auto;
}
.logo {
  margin-right: 0.54rem;
  width: 3.76rem;
  height: 0.52rem;
  background: url("../../assets/images/logo.png") no-repeat 0 0 / 100% auto;
}
.phone {
	position: absolute;
	right: 0;	
  width: 0.65rem;
  height: 0.45rem;
  background: url("../../assets/images/phone.png") no-repeat 0 0 / 100% auto;
}

</style>


