<template>
<div>
	<headA></headA>
	<activity :activity="activity"></activity>
	<footerA></footerA>
</div>
	
</template>

<script lang="babel">
import footerA from "./common/footer"; 
import headA from "./common/haed"; 
import activity from "./common/activity";
	export default{
		data(){
			return{
				activity:[],
			}
		},
        components:{
      	footerA,
      	headA,
      	activity
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	_this.$http({
        		method:"get",
        		url:"./static/json/introduce.json",
        		params:{},
        	})
        	.then(function(data){
				console.log(data);
				_this.activity=data.body;
			},function(err){
        		alert("请求失败");
        	});
        	
        }

    }
</script>
<style  >

</style>



