<template>
<div>
	<headA></headA>
	<introduce :introduceArr="introduceArr"></introduce>
	<footerA></footerA>
</div>
	
</template>

<script lang="babel">
import footerA from "./common/footer"; 
import headA from "./common/haed"; 
import introduce from "./common/introduce";
	export default{
		data(){
			return{
				introduceArr:[],
			}
		},
        components:{
      	footerA,
      	headA,
      	introduce
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	_this.$http({
        		method:"get",
        		url:"./static/json/introduce.json",
        		params:{},
        	})
        	.then(function(data){
				console.log(data);
				_this.introduceArr=data.body;
			},function(err){
        		alert("请求失败");
        	});
        	
        }

    }
</script>
<style  >

</style>



