<template>
<div>
	<headA></headA>
	<div v-for="obj in detail">
		<section class="det" v-if="!!obj.course">{{obj.course}}</section>
		<section class="detailsimg">
			<div class="content">
				<router-link :to="obj.href"><img :src="obj.img"/></router-link>
				<ul class="situation">
					<li>活动时间:{{obj.title}}</li>
					<li>活动对象:{{obj.objP}}</li>
					<li>活动地点:{{obj.place}}</li>
				</ul>
				<router-link :to="obj.href"><div class="button">报名参加</div></router-link>
			</div>
		</section>
	</div>
	<footerA></footerA>
</div>	
</template>

<script lang="babel">
import footerA from "./common/footer"; 
import headA from "./common/haed";
	export default{
		data(){
			return{
				detail:[]
			}
		},
        components:{
        	footerA,
        	headA
        },
        mounted(){//等加载完毕后
//      	console.log(this.$http);
        	let _this=this;
        	_this.$http({
        		method:"get",
        		url:"./static/json/detail.json",
        		params:{},
        	})
        	.then(function(data){
				console.log(data);
				_this.detail=data.body;
			},function(err){
        		alert("请求失败");
        	});
        	
        }
  
    }
</script>
<style  >
.det {
  height: 0.49rem;
  background: url("../assets/images/bg2_04.jpg") no-repeat 0 0 / 100% auto;
  padding: 0.21rem 0  0 0.3rem;
  line-height: 0.30rem;color: #009bda;
}
.detailsimg{height: auto;padding: 0.20rem 0 0rem 0.25rem;background: #f1f2f2;}
.content{background:#fff;margin-bottom: 0.50rem;width: 5.90rem;padding-bottom: 0.14rem;}
.content img {height: auto;}
.situation{line-height: 0.38rem;padding-left: 0.22rem;font-size: 0.24rem;}
.button{margin: 0.22rem 0 0 0.12rem;width: 5.65rem;height:0.72rem ;text-align: center;background: url("../assets/images/buttom.png") no-repeat 0 0 / 100% auto;color: #fff;line-height: 0.72rem;font-size: 0.29rem;}
</style>



