<template>
<div class="kuang" >
	<div>
		<div>
			<headA></headA>
			<div class="hieed">
		   <section class="switch"@touchstart="jsScroll" @touchmove="jsScroll" @touchend="jsScroll":style="{width:sliderArr.length*100+'%',left:moveX+'px'}" :class="{sliderAni:sliderAniBool}">
		   			<div class="img-1" v-for="obj in sliderArr"><img :src="obj.imgUrl"/></div>
			</section>
				<div class="point">
						<span v-for="(span,index) in sliderArr" :class="{color:(index==endX)}"></span>

				</div>
			</div>
				<div class="project">
					<div class="projectdw">
						<ul>
						<li ><router-link to="/brand"><img src="../assets/images/project1.png"/></router-link></li>
						<li><router-link to="/mba"><img src="../assets/images/project2.png"/></router-link></li>
						</ul>
						<ul>
						<li><router-link to="/introduce"><img src="../assets/images/project3.png"/></router-link></li>			
						<li><router-link to="/news"><img src="../assets/images/project4.png"/></router-link></li>
						</ul>
						<ul>
						<li><router-link to="/activity"><img src="../assets/images/project5.png"/></router-link></li>
						<li ><router-link to="/audition"><img src="../assets/images/project6.png"/></router-link></li>
						</ul>
					</div>
				</div>
				<footerA></footerA>
				
		</div>
	</div>
</div>
</template>

<script lang="babel">
import footerA from "./common/footer"; 
import headA from "./common/haed";
import brand from "./brand";
import bus from "../assets/js/bus";
	export default{
		data(){
			return{
				start:0,//起始坐标
				end:0,//结束坐标
				moveX:0,//表示移动距离
				endX:0,//当前位置
				showHideBool:false,//显示fix层的boolean值
				loadHideBool:false,//加载时让fix不做动画
				sliderAniBool:false,//是否让幻灯片做动画
				sliderArr:[],
				boolArr:{},
				}
		},
		methods:{
			jsScroll(e){
				var toucher=e.changedTouches[0];//得到一个触点
				var _this=this;
				var scrollLeft=document.body.scrollLeft+document.documentElement.scrollLeft;
				var switchW=$(".switch");
				var actualWidth=switchW.scrollWidth;//实际可视宽度
				var switchDiv=document.querySelector('.switch div');
				var switchDivAll=$('.switch div');
				var Width=switchDiv.clientWidth;//可视宽度
				if(e.type!="touchend"){
                    var target=e.targetTouches[0];
                    var pageX=target.pageX;
                    var pageY=target.pageY;
               	}
				if(e.type=="touchstart"){
					_this.start=toucher.pageX;
					_this.sliderAniBool=false;
//				console.log(toucher.pageX,Width)											
				}
				else if(e.type=="touchmove"){
				_this.end=toucher.pageX;
//				console.log(-Width,imgall.length,_this.end,_this.start)
				_this.moveX=-Width*_this.endX+_this.end-_this.start;
				if(Math.abs(_this.end-_this.start)>0)
				{
                        e.preventDefault();//消除默认效果
                    }
				}
				else if(e.type=="touchend"){
				var width5=Width/5;
//				 console.log(-_this.endX,Width,_this.sliderAniBool)
				if(_this.end-_this.start>width5){//手指向右
                        if(_this.endX!=0){
                            _this.endX--;               
                        }
                    }
                    else if(_this.end-_this.start<-width5){//手指向左
                        if(_this.endX!=_this.sliderArr.length-1){
                            _this.endX++;
                           
	                        }
	                    }
                    _this.moveX=-_this.endX*Width;
                    _this.sliderAniBool=true;
                    
					}
				}
	
		},
        components:{
      	footerA,
      	headA,
      	brand
        },
        mounted(){//
        	
            var _this=this;
            bus.$on("bool",function(boolF,boolC){
        		
        		_this.boolArr={showHideBool:boolF,loadHideBool:boolC};
        		console.log(_this.boolArr);
        	});
            _this.$http({//ajax
                method:'get',//请求方式
                url:'./static/json/slider.json',//请求地址(接口)
                params:{}//data发送到后台数据
            }).then(function(data){//data后台响应的数据;success进入
                //赋值给sliderArr数组,
                _this.sliderArr=data.data;//同步值；或_this.sliderArr=data.body;
            },function(error){//error进入
                //error
            });
        }
    }
</script>
<style  >
/*中间*/
.kuang{height: 100%;position: absolute;overflow: hidden;margin: 0 auto;}
.sliderAni{transition:left .3s;}
.hieed{width: 100% ;height: 3.7rem;  position: relative;overflow: hidden;}
.switch {
  width: 400% ;
  height: 3.7rem;
  border-bottom: 0.01rem solid #7f858b;
  display: flex;
  position: absolute;
}
.switch div{text-align: center;font-size: 0.9;position: relative;z-index: 2;}
.switch div img{height: 100%;width: 100%;display: block;}
.point {
  position: absolute;
  top: 3.45rem;
  left: 50%;
  width: 0.8rem;
  margin-left: -0.4rem;
  height: 0.15rem;
  display: flex;
}
.point span {
	display: block;
  margin-left: 0.06rem;
  border-radius: 50%;
  width: 0.15rem;
  height: 0.15rem;
  background: #fff;
  z-index: 3;
}
.point span:nth-child(1) {
  margin-left: 0px;
}
.color {
  background: #bbb8b8 !important;
}
.project {
  height: 4.77rem;
  background: #f1f2f2;
}
.project ul li {
  margin: 0.13rem 0.1rem 0.11rem 0.11rem;
  width: 1.92rem;
  height: 1.48rem;
}
.projectdw {
  display: flex;
  margin: 0 auto;
  width: 6.4rem;
}
.projectdw a{position: relative;z-index: 4;}
.labelOut{
    animation: labelOut 0.5s forwards;
    -moz-animation: labelOut 0.5s forwards;	/* Firefox */
    -webkit-animation: labelOut 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: labelOut 0.5s forwards;	/* Opera */
}
@keyframes labelIn
{
    0%{ left: -100%;}
    100%{ left: 0;}
}

@-moz-keyframes labelIn /* Firefox */
{
    0%{ left: -100%;}
    100%{ left: 0;}
}

@-webkit-keyframes labelIn /* Safari 和 Chrome */
{
    0%{ left: -100%;}
    100%{ left: 0;}
}

@-o-keyframes labelIn /* Opera */
{
    0%{ left: -100%;}
    100%{ left: 0;}
}
.labelIn{
    animation: labelIn 0.5s forwards;
    -moz-animation: labelIn 0.5s forwards;	/* Firefox */
    -webkit-animation: labelIn 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: labelIn 0.5s forwards;	/* Opera */
}
@keyframes labelOut
{
    0%{ left: 0;}
    100%{ left: -100%;}
}

@-moz-keyframes labelOut /* Firefox */
{
    0%{ left: 0;}
    100%{ left: -100%;}
}

@-webkit-keyframes labelOut /* Safari 和 Chrome */
{
    0%{ left: 0;}
    100%{ left: -100%;}
}

@-o-keyframes labelOut /* Opera */
{
    0%{ left: 0;}
    100%{ left: -100%;}
}


.kuangIn{
    animation: kuangIn 0.5s forwards;
    -moz-animation: kuangIn 0.5s forwards;	/* Firefox */
    -webkit-animation: kuangIn 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: kuangIn 0.5s forwards;	/* Opera */
}
@keyframes kuangIn
{
    0%{ left: 0rem;}
    100%{ left: 2.84rem;}
}

@-moz-keyframes kuangIn /* Firefox */
{
    0%{ left: 0rem;}
    100%{ left: 2.84rem;}
}

@-webkit-keyframes kuangIn /* Safari 和 Chrome */
{
    0%{ left: 0rem;}
    100%{ left: 2.84rem;}
}

@-o-keyframes kuangIn /* Opera */
{
    0%{ left: 0rem;}
    100%{ left: 2.84rem;}
}
.kuangOut{
    animation: kuangOut 0.5s forwards;
    -moz-animation: kuangOut 0.5s forwards;	/* Firefox */
    -webkit-animation: kuangOut 0.5s forwards;	/* Safari 和 Chrome */
    -o-animation: kuangOut 0.5s forwards;	/* Opera */
}
@keyframes kuangOut
{
    0%{ left: -2.84rem;}
    100%{ left: 0rem;}
}

@-moz-keyframes kuangOut /* Firefox */
{
    0%{ left: -2.84rem;}
    100%{ left: 0rem;}
}

@-webkit-keyframes kuangOut /* Safari 和 Chrome */
{
    0%{ left: -2.84rem;}
    100%{ left: 0rem;}
}

@-o-keyframes kuangOut /* Opera */
{
    0%{ left: -2.84rem;}
    100%{ left: 0rem;}
}
</style>



